<template>
  <div class="container">
    <div class="P300_block"  >
      <div  v-for="(item, index) in word"  :class="['P300_block_item', selected_index.includes(index)?'P300_block_item--selected':'']" :key="index">
        <span class="P300_block_word">
          {{item}}
        </span>
        <span v-show="select_character == index" class='P300_block_item--select-character'></span>
      </div>
    <div class="P300_handler"><button @click="start_flash">start</button> <button @click="stop_flash">stop</button></div>
    </div>
  </div>
</template>
<script>
import { word, flash_array, target_index} from '../constents/config'
export default {
   components: {
   },
   data() {
    return {
      word,
      target_index,
      flash_array,
      flash_index: -1,
      timmer: null,
      timmer2: null,
      trail_index: 0,
      flash_current_index: 0
    }
   },
  computed:{
    selected_index() {
      if (this.flash_index < 0) {
        return []
      }
      if(this.flash_index <= 6){
        const array = new Array(6).fill(0).map((index, item) => {
          return (this.flash_index-1)* 6  + item
        })
        return array
      }
      
      const array = new Array(6).fill(0).map((index, item) => {
          return item* 6  + (this.flash_index - 7)
        })
        return array
    },
    select_character() {
      return this.target_index[this.trail_index]
    }
  },
  methods: {
    start_flash() {
      console.log('this is start flash')
      this.runing_flash(0)
    },
    stop_flash() {
      console.log('this is stop_flash')
      clearTimeout(this.timmer)
      clearTimeout(this.timmer2)
    },
    runing_flash(index) {
      if(this.trail_index >= this.target_index.length) {
        this.stop_flash()
        return
      }
      if(this.flash_current_index == 12){
        this.flash_current_index = 0
        this.trail_index += 1
        this.flash_current_index += 1
        this.flash_index = -1
        console.log(this.trail_index)
        this.timmer = setTimeout(() => {
          this.flash_index = this.flash_array[this.flash_current_index]
          this.runing_flash(this.flash_index)
        }, 1000);
      }else {
        this.flash_current_index += 1
        this.flash_index = this.flash_array[this.flash_current_index]
        this.timmer = setTimeout(() => {
          this.flash_index = -1
          this.timmer2 = setTimeout(() => {
          const index = this.flash_index = parseInt(Math.random() * 13 )
            this.runing_flash(index)
          }, 75);
        }, 100);
      }
    }
  }
};
</script>
<style>
.container {
  height: 100%;
}
.P300_block {
 width: 100%;
 height: 100%;
 background-color: black;
}
.P300_block_word {
  display: inline-block;
  width: 80%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.P300_block_item {
  display:inline-block;
  position: relative;
  width: calc(100% /6);
  min-width: 100px;
  height:calc(100% /7);
  min-height: 100px;
  background: black;
  font-size: 50px;
  color: #fff;
  text-align: center;
}


.P300_block_item span {
  line-height: 200%;
}

.P300_block_item--select-character {
  display: inline;
  position: absolute;
  background-color: yellow;
  width: 20px;
  height: 20px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.P300_block_item--selected span{
  background-color: white;
  color: black;
}

.P300_handler {
  text-align: center;
}

.P300_handler button {
  width: 200px;
  height: 30px;
  margin: 20px 100px;
}

</style>